import { connect } from "react-redux";

import styled from 'styled-components';

const VoteIcon = styled.i`
    font-size: 18px;
`

function Results({ results }) {
    return <div>
        {results.map(Result)}
    </div>
}

function Result({ total }) {
    return <div>
        <VoteIcon className="fas fa-arrow-up"/>
        {total}
        <VoteIcon className="fas fa-arrow-down"/>
    </div>
}

export default connect()(Results)